import * as React from 'react'

interface Props {
  text: string
}

export const ExampleComponent = ({ text }: Props) => {
  return <div>Example Component: {text}</div>
}

interface IForm {
  schema: IFormSchema
}
export const Form = ({ schema }: IForm) => {
  return <p>hi {JSON.stringify(schema)}</p>
}

export interface IElementSchema {
  type: string
  [data: string]: any
}

export interface IFormSchema extends Array<IElementSchema> {}

const elements: Record<string, (props: IElementProps) => {}> = {}
export function registerElement(
  type: string,
  render: (props: IElementProps) => {}
): void {
  elements[type] = render
}

interface IElementProps {
  name: string
  [data: string]: any
}

